
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

@charset "UTF-8";
.todoapp {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 550px;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
  background: #fff;
  z-index: 1;
  position: relative;
  /*
  Hack to remove background from Mobile Safari.
  Can't use it globally since it destroys checkboxes in Firefox
*/
}
.todoapp button {
    margin: 0;
    padding: 0;
    border: 0;
    background: none;
    font-size: 100%;
    vertical-align: baseline;
    font-family: inherit;
    font-weight: inherit;
    color: inherit;
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.todoapp :focus {
    outline: 0;
}
.todoapp .hidden {
    display: none;
}
.todoapp .todoapp {
    background: #fff;
    margin: 130px 0 40px 0;
    position: relative;
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}
.todoapp .todoapp input::-webkit-input-placeholder {
    font-style: italic;
    font-weight: 300;
    color: #e6e6e6;
}
.todoapp .todoapp input::-moz-placeholder {
    font-style: italic;
    font-weight: 300;
    color: #e6e6e6;
}
.todoapp .todoapp input::input-placeholder {
    font-style: italic;
    font-weight: 300;
    color: #e6e6e6;
}
.todoapp .todoapp h1 {
    position: absolute;
    top: -155px;
    width: 100%;
    font-size: 100px;
    font-weight: 100;
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
    -webkit-text-rendering: optimizeLegibility;
    -moz-text-rendering: optimizeLegibility;
    text-rendering: optimizeLegibility;
}
.todoapp .new-todo,
  .todoapp .edit {
    position: relative;
    margin: 0;
    width: 100%;
    font-size: 18px;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
    border: 0;
    color: inherit;
    padding: 6px;
    border: 1px solid #999;
    -webkit-box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
            box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.todoapp .new-todo {
    padding: 10px 16px 16px 60px;
    border: none;
    background: rgba(0, 0, 0, 0.003);
    -webkit-box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
            box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
}
.todoapp .main {
    position: relative;
    z-index: 2;
    border-top: 1px solid #e6e6e6;
}
.todoapp .toggle-all {
    text-align: center;
    border: none;
    /* Mobile Safari */
    opacity: 0;
    position: absolute;
}
.todoapp .toggle-all + label {
    width: 60px;
    height: 34px;
    font-size: 0;
    position: absolute;
    top: -52px;
    left: -13px;
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg);
}
.todoapp .toggle-all + label:before {
    content: '\276F';
    font-size: 22px;
    color: #e6e6e6;
    padding: 10px 27px 10px 27px;
}
.todoapp .toggle-all:checked + label:before {
    color: #737373;
}
.todoapp .todo-list {
    margin: 0;
    padding: 0;
    list-style: none;
}
.todoapp .todo-list li {
    position: relative;
    font-size: 24px;
    border-bottom: 1px solid #ededed;
}
.todoapp .todo-list li:last-child {
    border-bottom: none;
}
.todoapp .todo-list li.editing {
    border-bottom: none;
    padding: 0;
}
.todoapp .todo-list li.editing .edit {
    display: block;
    width: 506px;
    padding: 12px 16px;
    margin: 0 0 0 43px;
}
.todoapp .todo-list li.editing .view {
    display: none;
}
.todoapp .todo-list li .toggle {
    text-align: center;
    width: 40px;
    /* auto, since non-WebKit browsers doesn't support input styling */
    height: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    border: none;
    /* Mobile Safari */
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
}
.todoapp .todo-list li .toggle {
    opacity: 0;
}
.todoapp .todo-list li .toggle + label {
    /*
    Firefox requires `#` to be escaped - https://bugzilla.mozilla.org/show_bug.cgi?id=922433
    IE and Edge requires *everything* to be escaped to render, so we do that instead of just the `#` - https://developer.microsoft.com/en-us/microsoft-edge/platform/issues/7157459/
  */
    background-image: url("data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23ededed%22%20stroke-width%3D%223%22/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: center left;
    background-size: 36px;
}
.todoapp .todo-list li .toggle:checked + label {
    background-size: 36px;
    background-image: url("data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23bddad5%22%20stroke-width%3D%223%22/%3E%3Cpath%20fill%3D%22%235dc2af%22%20d%3D%22M72%2025L42%2071%2027%2056l-4%204%2020%2020%2034-52z%22/%3E%3C/svg%3E");
}
.todoapp .todo-list li label {
    word-break: break-all;
    padding: 15px 15px 15px 50px;
    display: block;
    line-height: 1.0;
    font-size: 14px;
    -webkit-transition: color 0.4s;
    transition: color 0.4s;
}
.todoapp .todo-list li.completed label {
    color: #d9d9d9;
    text-decoration: line-through;
}
.todoapp .todo-list li .destroy {
    display: none;
    position: absolute;
    top: 0;
    right: 10px;
    bottom: 0;
    width: 40px;
    height: 40px;
    margin: auto 0;
    font-size: 30px;
    color: #cc9a9a;
    -webkit-transition: color 0.2s ease-out;
    transition: color 0.2s ease-out;
    cursor: pointer;
}
.todoapp .todo-list li .destroy:hover {
    color: #af5b5e;
}
.todoapp .todo-list li .destroy:after {
    content: '\D7';
}
.todoapp .todo-list li:hover .destroy {
    display: block;
}
.todoapp .todo-list li .edit {
    display: none;
}
.todoapp .todo-list li.editing:last-child {
    margin-bottom: -1px;
}
.todoapp .footer {
    color: #777;
    position: relative;
    padding: 10px 15px;
    height: 40px;
    text-align: center;
    border-top: 1px solid #e6e6e6;
}
.todoapp .footer:before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 40px;
    overflow: hidden;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}
.todoapp .todo-count {
    float: left;
    text-align: left;
}
.todoapp .todo-count strong {
    font-weight: 300;
}
.todoapp .filters {
    margin: 0;
    padding: 0;
    position: relative;
    z-index: 1;
    list-style: none;
}
.todoapp .filters li {
    display: inline;
}
.todoapp .filters li a {
    color: inherit;
    font-size: 12px;
    padding: 3px 7px;
    text-decoration: none;
    border: 1px solid transparent;
    border-radius: 3px;
}
.todoapp .filters li a:hover {
    border-color: rgba(175, 47, 47, 0.1);
}
.todoapp .filters li a.selected {
    border-color: rgba(175, 47, 47, 0.2);
}
.todoapp .clear-completed,
  .todoapp html .clear-completed:active {
    float: right;
    position: relative;
    line-height: 20px;
    text-decoration: none;
    cursor: pointer;
}
.todoapp .clear-completed:hover {
    text-decoration: underline;
}
.todoapp .info {
    margin: 65px auto 0;
    color: #bfbfbf;
    font-size: 10px;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
    text-align: center;
}
.todoapp .info p {
    line-height: 1;
}
.todoapp .info a {
    color: inherit;
    text-decoration: none;
    font-weight: 400;
}
.todoapp .info a:hover {
    text-decoration: underline;
}
@media screen and (-webkit-min-device-pixel-ratio: 0) {
.todoapp .toggle-all,
    .todoapp .todo-list li .toggle {
      background: none;
}
.todoapp .todo-list li .toggle {
      height: 40px;
}
}
@media (max-width: 430px) {
.todoapp .footer {
      height: 50px;
}
.todoapp .filters {
      bottom: 10px;
}
}
